@layer base {
  * {
    border-color: var(--border);
  }
  :focus-visible {
    outline: 2px solid var(--ring);
    outline-offset: 2px;
  }
  html {
    scroll-behavior: smooth;
  }
  body {
    background-color: var(--background);
    color: var(--foreground);
    font-family: var(--font-sans);
    letter-spacing: -0.01em;
    line-height: 1.6;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: var(--font-serif);
    font-weight: 600;
    letter-spacing: -0.02em;
    line-height: 1.2;
  }
  h1 {
    font-size: 2.5rem;
    font-weight: 700;
  }
  @media (min-width: 768px) {
    h1 {
      font-size: 3.25rem;
    }
  }
  @media (min-width: 1024px) {
    h1 {
      font-size: 4rem;
    }
  }
  h2 {
    font-size: 2rem;
    font-weight: 700;
  }
  @media (min-width: 768px) {
    h2 {
      font-size: 2.75rem;
    }
  }
  @media (min-width: 1024px) {
    h2 {
      font-size: 3.5rem;
    }
  }
  h3 {
    font-size: 1.75rem;
    font-weight: 700;
  }
  @media (min-width: 768px) {
    h3 {
      font-size: 2.25rem;
    }
  }
  @media (min-width: 1024px) {
    h3 {
      font-size: 2.75rem;
    }
  }
  h4 {
    font-size: 1.5rem;
    font-weight: 700;
  }
  @media (min-width: 768px) {
    h4 {
      font-size: 1.875rem;
    }
  }
  h5 {
    font-size: 1.25rem;
    font-weight: 700;
  }
  @media (min-width: 768px) {
    h5 {
      font-size: 1.5rem;
    }
  }
  h6 {
    font-size: 1.125rem;
    font-weight: 700;
  }
  @media (min-width: 768px) {
    h6 {
      font-size: 1.25rem;
    }
  }
  p {
    font-size: 1rem;
    line-height: 1.625;
    font-family: var(--font-sans);
  }
  .transition-all {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  }
  ::-webkit-scrollbar {
    width: 8px;
  }
  ::-webkit-scrollbar-track {
    background: var(--background);
  }
  ::-webkit-scrollbar-thumb {
    background: var(--gold);
    border-radius: 4px;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: var(--gold-light);
  }
  .dark ::-webkit-scrollbar-track {
    background: var(--background);
  }
  .dark ::-webkit-scrollbar-thumb {
    background: var(--gold);
  }
}
@layer base {
  a:hover {
    color: var(--gold);
  }
  button:hover,
  .btn:hover {
    background-color: var(--gold);
    color: var(--charcoal);
    transition: all 0.3s ease-in-out;
  }
  .card:hover {
    border-color: var(--gold);
    box-shadow: 0 4px 12px rgba(212, 175, 55, 0.3);
    transition: all 0.3s ease-in-out;
  }
  h1:hover, h2:hover, h3:hover, h4:hover, h5:hover, h6:hover {
    color: var(--gold);
    transition: color 0.3s ease-in-out;
  }
}
nextjs-portal {
  display: none;
